@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
	--card-height: 40vw;
	--card-margin: 4vw;
	--card-top-offset: 1em;
	--numcards: 4;
	--outline-width: 0px;
}


* ::-webkit-scrollbar {
	display: none;
}

body {
	margin: 0;
	font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
		'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}



code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* * NAVBAR * */
.nav-underline,
.nav-underline::after,
.nav-underline::before {
	position: relative;
	transition: 0.3s;
}
.nav-underline::after {
	content: '';
	position: absolute;
	bottom: 0.5rem;
	left: 0;
	height: 1px;
	width: 100%;
	background-color: rgb(3 84 6 /1);
	transition: transform 0.3s;
	transform: scaleX(0);
	transform-origin: right;
}
.nav-underline:hover::after {
	transform: scaleX(1);
	transform-origin: left;
}

.nav-underline:hover {
	color: rgb(3 84 6 /1);
}
.navbar-brand {
	color: #efefef;
	font-weight: bold;
	font-size: 36px;
}
.nav-underline:focus,
.nav-underline:hover {
	color: rgb(3 84 6 /1);
}

/* * MAIN * */
.bg-custom-color {
	background-color: #f2f7f5;
}
.bg-custom-color-trans {
	background-color: #f2f7f5ce;
}

/* * HERO * */
.navbar-blur {
	backdrop-filter: blur(8px);
}
.hero-svg {
	content: url('../icons/plant.svg');
	width: 27px;
	margin-left: 10px;
	margin-top: 2px;
}
.hero-svg--mobile {
	content: url('../icons/plant.svg');
	width: 18px;
	margin-left: 5px;
	margin-top: 2px;
}
/* * BUTTON * */
.btn-svg:before {
	content: url('../icons/home-button.svg');
	width: 20px;
	float: left;
	margin-right: 5px;
	margin-top: 2px;
}
.btn-svg--mobile:before {
	content: url('../icons/home-button-mobile.svg');
	width: 20px;
	float: left;
	margin-right: 5px;
	margin-top: 2px;
}

/* * COMPANIES * */
.slider {
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
	height: 100px;
	margin: auto;
	overflow: hidden;
	position: relative;
	width: 960px;
}
.slider::before,
.slider::after {
	background: linear-gradient(to right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 0) 100%);
	content: '';
	height: 100px;
	position: absolute;
	width: 200px;
	z-index: 2;
}
.slider::after {
	right: 0;
	top: 0;
	transform: rotateZ(180deg);
}

.slider::before {
	left: 0;
	top: 0;
}
.slide-track {
	animation: scroll 40s linear infinite;
	display: flex;
	width: calc(64px * 14);
}

.slide {
	height: 100px;
	width: 250px;
}

/* * STATS * */
.stat-item::after {
	content: '';
	position: absolute;
	height: 100%;
	width: 2px;
	background: grey;
	right: -9px;
}

/* * OFFERS * */
.animate {
	animation: 0.2s linear 0s alternate move;
}

@keyframes move {
	0%,
	25% {
		transform: translateX(-100%);
		left: 100%;
	}
	75%,
	100% {
		transform: translateX(0%);
		left: 0%;
	}
}

/* * SCROLLBAR * */
.scrollbar-hide::-webkit-scrollbar {
	display: none;
}

/* For IE, Edge and Firefox */
.scrollbar-hide {
	/* -ms-overflow-style: none; IE and Edge */
	scrollbar-width: none; /* Firefox */
}

