:root {
	--card-height: 40vw;
	--card-height-mobile: 60vw;
	--card-margin: 4vw;
	--card-margin-mobile: 60vw;
	--card-top-offset: 1rem;
	--card-top-offset-mobile: 10rem;
	--card-top-offset-card: 12rem;
	--card-top-offset-card-mobile: 11rem;
	--numcards: 4;
	--outline-width: 0px;
	--switch-margin: 37vw;
	--switch-margin-lg: 50vw;
	--switch-margin-mac: 35vw;
	--switch-margin-mobile: 200vw;
}

#cards {
	padding-bottom: calc(
		var(--numcards) * var(--card-top-offset) * 0.8
	); /* Make place at bottom, as items will slide to that position*/
	margin-bottom: calc(
		var(--card-margin) 
	); /* Don't include the --card-margin in padding, as that will affect the scroll-timeline*/
}

#card_1 {
	--index: 1;
	--multipler: 1.2;
	--multipler-scale: 1.1;
	--multipler-mobile: 1.1;
	--multipler-mobile-t: 0.53;
}

#card_2 {
	--index: 2;
	--multipler: 0.8;
	--multipler-scale: 0.75;
	--multipler-mobile: 0.7;
	--multipler-mobile-t: 0.21;
}

#card_3 {
	--index: 3;
	--multipler: 0.67;
	--multipler-scale: 0.63;
	--multipler-mobile: 0.57;
	--multipler-mobile-t: 0.01;
}

#card_4 {
	--index: 4;
	--multipler: 0.6;
	--multipler-scale: 0.57;
	--multipler-mobile: 0.39;
	--multipler-mobile-t: 0.01;
}


.card {
	padding-top: calc(var(--index) * var(--card-top-offset-card) * var(--multipler));
}

.systemMargin {
	margin-bottom: calc(var(--switch-margin));
}

/** PAGE STYLING **/

#cards {
	list-style: none;
	outline: calc(var(--outline-width) * 10) solid blue;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: repeat(var(--numcards), var(--card-height));
	gap: var(--card-margin);
}

.card {
	outline: var(--outline-width) solid hotpink;
}
@media(max-width: 1513px)
{
	.systemMargin{
        margin-bottom: calc(var(--switch-margin-mac));
        }
}
@media (max-width:1280px){
	.card {
		padding-top: calc(var(--index) * var(--card-top-offset-card) * var(--multipler-scale));
	}
	#cards {
		padding-bottom: calc(
			var(--numcards) * var(--card-top-offset) * 4
		); /* Make place at bottom, as items will slide to that position*/
		margin-bottom: calc(
			var(--card-margin) 
		); /* Don't include the --card-margin in padding, as that will affect the scroll-timeline*/
	}
	.systemMargin{
        margin-bottom: calc(var(--switch-margin-lg));
        }
}
@media (max-width: 768px) {
	#cards {
		padding-bottom: calc(
			var(--numcards) * var(--card-top-offset) * 3.5
		); /* Make place at bottom, as items will slide to that position*/
		margin-bottom: var(
			--card-margin-mobile
		); /* Don't include the --card-margin in padding, as that will affect the scroll-timeline*/
		gap: var(--card-margin-mobile);
	}
	#cards {
		list-style: none;
		outline: calc(var(--outline-width) * 10) solid blue;
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: repeat(var(--numcards), var(--card-height-mobile));
	}
	.card {
		padding-top: calc(var(--index) * var(--card-top-offset-card-mobile) * var(--multipler-mobile));
		padding-bottom: calc(
			var(--index) * var(--card-top-offset-card-mobile) * var(--multipler-mobile) * var(--multipler-mobile-t)
		);
	}
	.systemMargin{
        margin-bottom: calc(var(--switch-margin-mobile));
        }
}
